import { Component } from '@angular/core';
import {  ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { pdfx } from '../../../../providers/pdf/pdfx';

@IonicPage()
@Component({
  selector: 'page-Unidadidiomanivel3-list',
  templateUrl: 'unidadidiomanivel3-list.html',
})
export class Unidadidiomanivel3ListPage {


  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
  idempresa:any;
  idafiliado:any='na';
  public idtipo                 : any;
  public idtipo2                 : any;
  public idtipo3                : any;
  public idnombre                 : any;
  ubica: any;
  fecha: any;

  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  itemsxxx:any;

  constructor( public Pdfx:  pdfx, public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })
  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }



  pideregistros() {
      let self = this;
      self.loading = true;

      this.regService.getReg(this.idtipo+'/'+ this.idtipo2 + '/' +  this.idtipo3,'unidadidiomanivel3s').then((data) => {

      this.items=data;
      this.items2=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    this.idtipo=this.navParams.data.record.idtipounidad;
    this.idtipo2=this.navParams.data.record.idunidadacademica;
    this.idtipo3=this.navParams.data.record._id;

    this.idnombre=this.navParams.data.record.nombre;
    this.itemsxxx=this.navParams.data.record

    this.pideregistros();


  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  salonRecord(item : any, no) : void
  {
    this.navCtrl.push('Unidadsalon3ListPage', { record : item });
  }

  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'unidadidiomanivel3s').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Unidadidiomanivel3CreatePage',  { record : item ,record2:{idtipo:this.idtipo,idtipo2:this.idtipo2,idtipo3:this.idtipo3,idnombre:this.idnombre}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('Unidadidiomanivel3CreatePage', { record : null ,record2:{idtipo:this.idtipo,idtipo2:this.idtipo2,idtipo3:this.idtipo3,idnombre:this.idnombre}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {

   }


   presentFilter() {

  }
}



